import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ParameterDataLevel2 } from "../../models/sensor.model"
import {SensorProvider} from "../../providers/sensor/sensor.provider"
import {BasePage} from "../../pages/base.page";
import {Events} from "ionic-angular/index";

/**
 * Generated class for the ParametersMenuPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-parameters-menu',
  templateUrl: 'parameters-menu.html',
})
export class ParametersMenuPage  extends BasePage{

	menu : ParameterDataLevel2 = null;
  title : string;

  constructor(public navCtrl: NavController, public navParams: NavParams,
  	private sensorProvider : SensorProvider,
    alert : AlertController,
    events : Events) {
        super(alert,events);
  			this.sensorProvider.sensor.subscribe(s => {
          var menu = this.navParams.get('menu');
          this.title = menu.name;
          this.menu = this.sensorProvider.loadMenu(menu.id);
          this.sensorProvider.setupMessageQueue();
  			})
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ParametersMenuPage');
  }

  goHome(){
  	this.navCtrl.setRoot('HomePage');
  }

  goBack(){
  	this.navCtrl.pop();
  }

  addToHome(){

  }

}



